import React, { useState, useEffect } from 'react';
import './App.css';

const setTime = () => {
  const targetDate = new Date('09-25-2021');
  const targetTime = targetDate.getTime();
  const nowTime = Date.now();
  const _seconds = Math.floor((targetTime - nowTime) / 1000);
  const days = Math.floor(_seconds / 86400);
  const hours = Math.floor((_seconds - days * 86400) / 3600);
  const minutes = Math.floor((_seconds - hours * 3600 - days * 86400) / 60);
  const seconds = _seconds - minutes * 60 - hours * 3600 - days * 86400;
  
  return {
    minutes: `${minutes}`.padStart(2, '0'),
    hours: `${hours}`.padStart(2, '0'),
    days: `${days}`.padStart(2, '0'),
    seconds: `${seconds}`.padStart(2, '0'),
  }
}

function App() {
  const [date, setDate] = useState(setTime());
  const [visible, setVisible] = useState(true)

  useEffect(() => {
    const id = setTimeout(() => {
      setDate(setTime())
      setVisible(!visible);
    }, 1000);
    return () => {
      clearTimeout(id);
    }
  })
  return (
    <div className="wrapper">
      <h1 className="title">ğŸŠğŸ¥‚ğŸ’ğŸ‘©â€â¤ï¸â€ğŸ’‹â€ğŸ‘¨ğŸ’ğŸ¥‚ğŸŠ</h1>
      <div className="timer">
        <span className="time days">
          <span className="time__value">{date.days}</span>
          <span className="time__title">Days</span>
        </span>
        :
        <span className="time hours">
          <span className="time__value">{date.hours}</span>
          <span className="time__title">Hours</span>
        </span>
        :
        <span className="time hours">
          <span className="time__value">{date.minutes}</span>
          <span className="time__title">Minutes</span>
        </span>
        :
        <span className="time seconds">
          <span className="time__value">{date.seconds}</span>
          <span className="time__title">Seconds</span>
        </span>
      </div>
    </div>
  );
}

export default App;
